import { Component, OnInit } from '@angular/core';

import { GamesService } from 'src/app/services/games.service';

@Component({
  selector: 'app-playstation5',
  templateUrl: './playstation5.component.html',
  styleUrls: ['./playstation5.component.css']
})
export class Playstation5Component implements OnInit {

  games = null
  page:number = 0

  constructor(private gamesService:GamesService) { 
    this.LoadGames()
  }

  ngOnInit(): void {
  }

  LoadGames = async() =>{
    this.games = await this.gamesService.getGamesByPlatform('187').subscribe((resp:any) =>{
     this.gamesService.resizeImages(resp.results)
     this.games = resp.results
     this.page += 1
   }, (err) => {
       console.log(err)
       this.games = null
     })

   }


}
