import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { PlaystationComponent } from './playstation/playstation.component';
import { NintendoComponent } from './nintendo/nintendo.component';
import { XboxComponent } from './xbox/xbox.component';
import { PcComponent } from './pc/pc.component';
import { PhoneComponent } from './phone/phone.component';
import { Playstation2Component } from './playstation/playstation2/playstation2.component';
import { Playstation3Component } from './playstation/playstation3/playstation3.component';
import { Playstation4Component } from './playstation/playstation4/playstation4.component';
import { Playstation5Component } from './playstation/playstation5/playstation5.component';
import { WiiuComponent } from './nintendo/wiiu/wiiu.component';
import { WiiComponent } from './nintendo/wii/wii.component';
import { SwitchComponent } from './nintendo/switch/switch.component';
import { ClassicComponent } from './xbox/classic/classic.component';
import { X360Component } from './xbox/x360/x360.component';
import { OneComponent } from './xbox/one/one.component';
import { SeriesComponent } from './xbox/series/series.component';
import { N3dsComponent } from './nintendo/n3ds/n3ds.component';
import { PspComponent } from './playstation/psp/psp.component';
import { VitaComponent } from './playstation/vita/vita.component';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';


const routes: Routes = [
    {path: 'playstation', children:[
        {path: '', component: PlaystationComponent},
        {path: '2', component: Playstation2Component},
        {path: '3', component: Playstation3Component},
        {path: '4', component: Playstation4Component},
        {path: '5', component: Playstation5Component},
        {path: 'psp', component: PspComponent},
        {path: 'vita', component: VitaComponent}
    ]},  
    {path: 'nintendo', children:[
        {path: '', component: NintendoComponent},
        {path: 'wii', component: WiiComponent},
        {path: 'wiiu', component: WiiuComponent},
        {path: '3ds', component: N3dsComponent},
        {path: 'switch', component: SwitchComponent}
    ]},
    {path: 'xbox', children:[
        {path: '', component:XboxComponent},
        {path: 'classic', component: ClassicComponent},
        {path: '360', component: X360Component},
        {path: 'one', component: OneComponent},
        {path: 'series', component: SeriesComponent}
    ]},
    {path: 'pc', component: PcComponent},
    {path: 'phone', component: PhoneComponent}
];

@NgModule({
    imports: [RouterModule.forChild(routes), CommonModule],
    exports: [RouterModule]
})
export class PlatformsRoutingModule {}
