import { Component, OnInit } from '@angular/core';

import { GamesService } from 'src/app/services/games.service';

@Component({
  selector: 'app-n3ds',
  templateUrl: './n3ds.component.html',
  styleUrls: ['./n3ds.component.css']
})
export class N3dsComponent implements OnInit {

  games = null
  page:number = 0

  constructor(private gamesService:GamesService) {
   this.LoadGames() 
   }

  ngOnInit(): void {
  }

  LoadGames = async() =>{
    this.games = await this.gamesService.getGamesByPlatform('19').subscribe((resp:any) =>{
     this.gamesService.resizeImages(resp.results)
     this.games = resp.results
     this.page += 1
   }, (err) => {
       console.log(err)
       this.games = null
     })
  }
}
