import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { User } from 'src/app/models/user.model';
import { FileUploadService } from 'src/app/services/file-upload.service';
import { UserService } from 'src/app/services/user.service';

declare function customInitFunctions();
declare function toastMessage(message, color)
declare function selectInit()

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})


export class UserComponent implements OnInit {

  user:User

  editForm = this.fb.group({
    name: [, [Validators.required, Validators.min(1), Validators.max(40)]],
    username: [, [Validators.required, Validators.min(3), Validators.max(20)]],
    email: [, [Validators.required, Validators.email]],
    acceptInvitations: [, Validators.required]
  })

  constructor (private http:HttpClient, private userService:UserService,private fb:FormBuilder, private fileUploadService:FileUploadService) { 
    customInitFunctions()
    // this.loadInfo()
   this.user = userService.user
   this.initForm()
  }
  
  initForm(){
    this.editForm = this.fb.group({
      name: [this.user.name, [Validators.required, Validators.min(1), Validators.max(15)]],
      username: [this.user.username, [Validators.required, Validators.min(3), Validators.max(20)]],
      email: [this.user.email, [Validators.required, Validators.email]],
      acceptInvitations: [this.user.acceptInvitations, Validators.required]
    })
    selectInit()
  }
  
  ngOnInit(): void {
    if(localStorage.getItem('token')){
      this.userService.validateToken().subscribe(resp =>{
        this.user = this.userService.user
      })
    }
  }


  updateUser(){
    if(this.editForm.invalid){
      toastMessage('Please verify your information', 'red accent-4')
      return
    }
    this.userService.updateUser(this.editForm.value).subscribe((resp:any) =>{
      toastMessage(resp.message, "green darken-1")
    }, (err) => {
      if (err.error.err.keyPattern.email){
        toastMessage("There is another user using this EMAIL", "red accent-4")
      }
      else if(err.error.err.keyPattern.username){
        toastMessage("There is another user using this USERNAME", "red accent-4")
      }
    })
  }


  changeImg(img:File){
    if(img.size > 2000000){
      toastMessage("The image size cant' moore than 2mb", "red accent-4")
      return
    }

    const extension = img.name.split('.').slice(-1).toString()
    // Validate extension
    const validExtensions = ['png', 'jpg', 'jpeg']

    if(!validExtensions.includes(extension)){
      toastMessage('The extension is not valid, please use: "JPG", "PNG", "JPEG"', "red accent-4")
      return
    }
  
    this.fileUploadService.updateImg(img, 'users', this.user.uid)
    .then(imgDB =>{ 
      this.userService.user.img = imgDB
      toastMessage("Updated image successfully", "green")
    }).catch(err => toastMessage(err, 'red accent-4'))
  }

}