import { Component, OnInit } from '@angular/core';

import { GamesService } from 'src/app/services/games.service';

@Component({
  selector: 'app-playstation4',
  templateUrl: './playstation4.component.html',
  styleUrls: ['./playstation4.component.css']
})
export class Playstation4Component implements OnInit {

  games = null
  page:number = 0


  constructor(private gamesService:GamesService) { }

  ngOnInit(): void {
  }

  LoadGames = async() =>{
    this.games = await this.gamesService.getGamesByPlatform('18').subscribe((resp:any) =>{
     this.gamesService.resizeImages(resp.results)
     this.games = resp.results
     this.page += 1
   }, (err) => {
       console.log(err)
       this.games = null
     })

   }

}
