import { Component, OnInit } from '@angular/core';

import { GamesService } from 'src/app/services/games.service';

@Component({
  selector: 'app-playstation3',
  templateUrl: './playstation3.component.html',
  styleUrls: ['./playstation3.component.css']
})
export class Playstation3Component implements OnInit {

  games = null
  page:number = 0

  constructor(private gamesService:GamesService) { }

  ngOnInit(): void {
    this.LoadGames()
  }

  LoadGames = async() =>{
    this.games = await this.gamesService.getGamesByPlatform('16').subscribe((resp:any) =>{
     this.gamesService.resizeImages(resp.results)
     this.games = resp.results
     this.page += 1
   }, (err) => {
       console.log(err)
       this.games = null
     })

   }

   gameMatch(game){
     console.log(game)
   }

}
