import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatchService } from 'src/app/services/match.service';
import { PlatformService } from 'src/app/services/platform.service';

@Component({
  selector: 'app-match',
  templateUrl: './match.component.html',
  styleUrls: ['./match.component.css']
})
export class MatchComponent implements OnInit {

  match
  platform
  loading = true

  constructor(private matchService:MatchService, private activatedRoute:ActivatedRoute, private platformService:PlatformService) { 
    this.activatedRoute.params.subscribe(params =>{
      matchService.getMatch(params.match).subscribe(resp =>{
        this.match = resp
        this.match.date = new Date(this.match.date)
        platformService.getPlatformInfo(this.match.platform).subscribe(platform =>{
          this.platform = platform
          this.loading = false
        })
      }, (err) => console.error(err))
    })
  }

  ngOnInit(): void {
  }

}
