import { Component, OnInit } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from "@angular/common";
import { GamesService } from 'src/app/services/games.service';

declare function customInitFunctions()

@Component({
  selector: 'app-playstation2',
  templateUrl: './playstation2.component.html',
  styleUrls: ['./playstation2.component.css']
})
export class Playstation2Component implements OnInit {

  color = "dark"
  games = null
  page:number = 0
  constructor(private gamesService:GamesService) {
    this.LoadGames()
   }

   test = true  
  ngOnInit(): void {
    customInitFunctions()
  }

  LoadGames = async() =>{
    this.games = await this.gamesService.getGamesByPlatform('15').subscribe((resp:any) =>{
     this.gamesService.resizeImages(resp.results)
     this.games = resp.results
     this.page += 1
   }, (err) => {
       console.log(err)
     })

   }

   gameMatch(game){
     console.log(game)
   }

}
